.catalog {
	width: 100%;
	margin-top: 32px;




	&__list {
		padding: 0;
		margin-top: 38px;
		margin-bottom: 30px;
		box-sizing: border-box;
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		box-sizing: border-box; }

	&_flex {
		display: flex;
		margin-top: 15px; }




	&__item {
		list-style: none;
		width: 25%;
		margin-bottom: 50px;

		&_mobile {
			@media screen and (max-width:992px) {
				width: 50%; }

			@media screen and (max-width:460px) {
				width: 100%; } }

		&_margin-bottom_no {
			margin-bottom: 0; }


		&-title {
			margin-top: 10px;
			max-width: 171px; }

		&-link {
			text-decoration: none; }

		&-img {
			height: 60px;
			width: auto; } }






	&-item {


		&__sum {
			text-decoration: none;
			font-family: "RobotoRegular", sans-serif;
			font-size: 14px;
			line-height: 1.2em;
			color: $light-gray-text;
			display: inline-block;
			cursor: pointer; }

		&__inner {

			&-list {
				padding-left: 0;
				display: flex;
				flex-direction: column; }

			&-item {
				padding-left: 0;
				list-style: none;
				margin-bottom: 10px;
				position: relative;
				padding-left: 25px; }



			&-link {
				font-family: "RobotoRegular", sans-serif;
				font-size: 14px;
				line-height: 1.2em;
				letter-spacing: 0.02em;
				color: $primary-blue;
				position: relative;
				margin-right: 5px;
				display: inline-block;

				&:hover {
					text-decoration: none; } }


			&-open {
				position: absolute;
				content: '';
				width: 15px;
				height: 15px;
				border-radius: 4px;
				border: 1px solid $border;
				left: 0;
				display: inline-block;

				&:hover {
					cursor: pointer; }


				&::before {
					position: absolute;
					content: '';
					width: 6px;
					height: 1px;
					background-color: #818183;
					transform: rotate(90deg);
					left: 5px;
					top: 7px; }



				&::after {
					position: absolute;
					content: '';
					width: 6px;
					height: 1px;
					background-color: #818183;
					left: 5px;
					top: 7px; }


				&_active::before {
					display: none; } } }


		&__more {
			font-family: "RobotoBold", sans-serif;
			font-size: 14px;
			line-height: 0.02em;
			text-decoration: none;
			display: inline-block;
			color: $primary-color;
			padding: 4px 0;
			margin-left: 27px;
			height: 14px;
			box-sizing: border-box;
			border-bottom: 1px dashed $primary-color;
			position: relative;

			&::before {
				content: '';
				width: 18px;
				height: 4px;
				position: absolute;
				left: -27px;
				background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2218%22%20height%3D%224%22%20viewBox%3D%220%200%2018%204%22%20fill%3D%22none%22%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%3E%0A%3Cpath%20d%3D%22M4%202C4%203.10457%203.10457%204%202%204C0.89543%204%200%203.10457%200%202C0%200.89543%200.89543%200%202%200C3.10457%200%204%200.89543%204%202Z%22%20fill%3D%22%23D9D9D9%22/%3E%0A%3Cpath%20d%3D%22M11%202C11%203.10457%2010.1046%204%209%204C7.89543%204%207%203.10457%207%202C7%200.89543%207.89543%200%209%200C10.1046%200%2011%200.89543%2011%202Z%22%20fill%3D%22%23D9D9D9%22/%3E%0A%3Cpath%20d%3D%22M18%202C18%203.10457%2017.1046%204%2016%204C14.8954%204%2014%203.10457%2014%202C14%200.89543%2014.8954%200%2016%200C17.1046%200%2018%200.89543%2018%202Z%22%20fill%3D%22%23D9D9D9%22/%3E%0A%3C/svg%3E%0A');
				background-repeat: no-repeat;
				background-position: center; }



			&:hover {
				border-bottom: transparent; } } } }




.catalog-item-inner {

	&__list {
		display: none;
		padding-left: 0;
		margin-top: 8px;

		&_active {
			display: block; } }

	&__item {
		list-style: none;
		margin-bottom: 5px; }

	&__link {
		color: $primary-gray;
		text-decoration: none;

		&:hover {
			text-decoration: underline; } } }



