.layout-controls {

	display: flex;

	&__title {
		margin-right: 20px;
		font-family: "RobotoRegular", sans-serif;
		font-size: 12px;
		color: $page-text;
		letter-spacing: 0.02em; }

	&__list {
		cursor: pointer;
		margin-right: 14px;

		&_active {
			svg {
				fill: $primary-color; } }


		svg {
			width: 15px;
			height: 12px;
			fill: #c4c4c4; } }



	&__table {
		cursor: pointer;

		&_active {
			svg {
				fill: $primary-color; } }


		svg {
			width: 15px;
			height: 12px;
			fill: #c4c4c4; } } }


