.slideshow {
	width: 100%;
	background-color: #fff;
	border: 1px solid $border;
	border-radius: 8px;
	padding: 12px 18px;
	display: flex;
	box-sizing: border-box;




	&__fullsize {

		padding: 38px 68px 26px 74px;
		box-sizing: border-box;

		@media screen and (max-width:992px) {
			padding: 26px 40px 0 32px; }

		@media screen and (max-width:460px) {
			padding: 18px 28px 43px 12px; }


		&-img {
			max-width: 362px;
			max-height: 283px;
			width: 100%;
			height: auto; } }




	&__thumbs {
		padding: 0;
		margin-top: 0;
		margin-bottom: 0;
		margin-left: auto;


		&-img {
			max-width: 51px;
			max-height: 40px;
			width: 100%;
			height: auto;

			@media screen and (max-width:992px) {
				max-width: 31px; }

			@media screen and (max-width:460px) {
				max-width: 28px; } }

		&-link {
			width: 100%;
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center; }



		&-item {
			list-style: none;
			width: 60px;
			height: 50px;
			display: flex;
			justify-content: center;
			align-items: center;
			margin-bottom: 8px;
			border: 1px solid transparent;
			box-sizing: border-box;

			&:last-child {
				margin-bottom: 0; }

			@media screen and (max-width:992px) {
				width: 42px;
				height: 36px; }

			@media screen and (max-width:460px) {
				width: 42px;
				height: 36px;
				margin-bottom: 5px; }

			&_active {
				border: 1px solid $border; } } }


	&_col_6 {
		width: 67%;
		margin-right: 20px;

		@media screen and (max-width:767px) {
			width: 100%; } } }

