.overlay-img {
	position: fixed;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	background-color: rgba(0,0,0,.45);
	display: none;
	z-index: 1500;

	&__inner {

		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		max-width: 830px;
		width: 90%;
		height: auto;

		img {
			width: 100%;
			height: 100%; } }

	&__close {
		position: absolute;
		content: '';
		width: 24px;
		height: 24px;
		top: 0;
		right: -40px;
		background: url('../img/cross.svg') no-repeat center;
		cursor: pointer;

		&:hover {
			opacity: .7; } }


	&_active {
		display: block; } }
