.cart {

	&_margin-right_m {
		@media screen and (max-width:767px) {
			margin-right: 20px; } }

	&__wrap {
		display: flex;
		max-width: 397px;
		width: 100%;

		@media screen and (max-width:767px) {
			margin-top: 15px; } }

	&__header {
		display: flex;
		padding-bottom: 7px;
		min-width: 100px; }

	&__sum {
		margin-bottom: 10px; }

	&__sum-price {
		margin-bottom: 10px; }

	&__sub-title {

		&_m {
			display: none;
			font-family: "RobotoRegular", sans-serif;
			color: $primary-gray;
			letter-spacing: 0.02em;
			font-size: 12px;
			margin-bottom: 10px;

			@media screen and (max-width:767px) {
				display: block; } } }


	&__print {
		margin-left: auto;
		align-self: flex-start;
		position: absolute;
		right: 0;
		top: 0;


		svg {
			width: 18px;
			height: 17px; } }

	&__back {

		color: $primary-color;
		text-decoration: none;
		position: relative;
		padding-left: 29px;
		margin-bottom: 18px;

		&::before {
			position: absolute;
			content: '';
			width: 13px;
			height: 14px;
			background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2213%22%20height%3D%2214%22%20viewBox%3D%220%200%2013%2014%22%20fill%3D%22none%22%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%3E%0A%3Cpath%20d%3D%22M6.96285%201.57396H3.62229V0L0%202.19527L3.62229%204.39053V2.81657H6.96285C9.61919%202.81657%2011.7926%205.05325%2011.7926%207.78698C11.7926%2010.5207%209.61919%2012.7574%206.96285%2012.7574H3.62229V14H6.96285C10.3034%2014%2013%2011.2249%2013%207.78698C13%204.34911%2010.3034%201.57396%206.96285%201.57396Z%22%20fill%3D%22%23EC6D26%22/%3E%0A%3C/svg%3E%0A');
			background-repeat: no-repeat;
			background-position: center;
			top: 2px;
			left: 3px; }

		&_m {
			margin-top: 15px;
			display: none;

			@media screen and (max-width:767px) {
				display: block; }

			@media screen and (max-width:460px) {
				align-self: center; } } }


	&__issue {
		height: 54px;
		width: 270px;
		margin-top: 25px;

		@media screen and (max-width:460px) {
			width: 100%; } }


	&__row {
		display: flex;
		position: relative; }

	&__column {
		display: flex;
		flex-direction: column;
		justify-content: space-between; }


	&__result {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		margin-right: 43px;

		&_m {
			@media screen and (max-width:460px) {
				width: 100%;
				margin-right: 0; } } }

	&__clear {
		color: $primary-color;
		text-decoration: none;
		position: relative;
		padding-left: 29px;

		&::before {
			position: absolute;
			content: '';
			width: 21px;
			height: 2px;
			background-color: $primary-color;
			transform: rotate(45deg);
			top: 9px;
			left: 0; }

		&::after {
			position: absolute;
			content: '';
			width: 21px;
			height: 2px;
			background-color: $primary-color;
			transform: rotate(-45deg);
			top: 9px;
			left: 0; }

		&_m {
			margin-top: 23px;
			margin-bottom: 15px;

			display: none;

			@media screen and (max-width:767px) {
				display: block; } } }

	&__item {
		display: flex;
		padding: 11px 0;
		border-bottom: 1px solid $border;

		@media screen and (max-width:767px) {
			flex-direction: column; } }



	&__item-img {
		margin-right: 14px;
		width: 98px;
		height: 98px;
		border: 1px solid $border;
		box-sizing: border-box;
		border-radius: 8px;
		padding: 12px;
		display: flex;
		align-items: center;
		background-color: #fff; }

	&__search {
		background-color: transparent;
		border: 1px solid $border;
		border-radius: 8px;
		margin-top: 15px;
		box-shadow: inset 0 0 5px rgba(0,0,0,.1);
		margin-bottom: 20px; }

	&__descr {
		padding-right: 15px; }


	&__tab {
		cursor: pointer;
		margin-right: 56px;

		@media screen and (max-width:767px) {
			margin-right: 0;
			order: 1;
			margin-top: 15px; }

		svg {
			width: 22px;
			height: 18px;
			fill: #D9D9D9; } }



	&__close {
		position: relative;
		width: 21px;
		height: 21px;
		cursor: pointer;

		&::before {
			position: absolute;
			content: '';
			width: 21px;
			height: 2px;
			background-color: $primary-color;
			transform: rotate(45deg);
			top: 8px; }

		&::after {
			position: absolute;
			content: '';
			width: 21px;
			height: 2px;
			background-color: $primary-color;
			transform: rotate(-45deg);
			top: 8px; } }

	&__controls {
		display: flex;




		&_m {
			display: none;

			@media screen and (max-width:767px) {
				display: flex;
				flex-direction: column;
				margin-left: auto; } } }



	&__img {}

	&__title {}

	&__art {
		margin-top: 10px; }

	&__price {
		font-family: "RobotoMedium", sans-serif;
		font-size: 20px;
		color: $primary-gray;

		@media screen and (max-width:460px) {
			font-size: 16px; } }

	&_padding {
		padding-bottom: 50px;
		border-bottom: 1px solid $border;

		@media screen and (max-width:460px) {
			padding-bottom: 17px; } }



	&_col_4 {
		max-width: 390px;
		width: 100%;
		display: flex;

		@media screen and (max-width:767px) {
			width: 100%;
			max-width: 100%; } }

	&_col_2 {
		max-width: 150px;
		width: 100%;

		&_s {

			@media screen and (max-width:992px) {
				max-width: 110px; } } }


	&_col_1 {
		max-width: 120px;
		width: 100%;
		display: flex; }

	&_shrink {
		flex-shrink: 1; }

	&__item-m {
		display: none;
		border-bottom: 1px solid $border;
		padding-bottom: 15px;
		padding-top: 15px;

		&:last-child {
			border-bottom: 1px solid transparent; }

		@media screen and (max-width:767px) {
			display: block; } }




	&__row-m {
		display: flex;
		justify-content: space-between;
		margin-top: 15px;

		.cart__art {
			margin-top: 0;
			display: flex;
			flex-direction: column; }

		.quantity {
			margin-top: 5px; }

		.popup__cart-price {
			align-self: center; } } }

.m-cart-popular {
	padding-left: 15px;
	overflow-x: hidden;
	box-sizing: border-box;
	display: none;

	@media screen and (max-width:767px) {
		display: block; }

	&__list {
		flex-wrap: nowrap;
		overflow-x: scroll; }

	&__item {
		width: 102px;
		flex-shrink: 0; } }

.active-good-tab {
	fill: $primary-color !important; }
