.header {

	&__container {
		max-width: 1200px;
		padding: 17px 15px 15px;
		width: 100%;
		box-sizing: border-box;
		margin-right: auto;
		margin-left: auto;
		display: flex;
		justify-content: space-between; }


	&__profit {
		max-width: 172px;

		&-title {
			font-family: "RobotoMedium",Arial, sans-serif;
			font-size: 14px;
			letter-spacing: .08em;
			color: $primary-gray;
			text-transform: uppercase;
			margin-bottom: 8px;
			line-height: 1.2; }

		&-descr {
			font-family: "RobotoRegular",Arial, sans-serif;
			font-size: 12px;
			letter-spacing: .02em;
			color: $header-descr;
			line-height: 1; } }

	&__contacts {

		&-item {
			margin-top: 0;
			margin-bottom: 10px;
			color: $header-contacts;
			font-size: 14px;

			&::before {
				top: 3px !important; } }

		&-download {
			cursor: pointer;
			color: $header-download-link;
			text-decoration: underline;


			&:hover {
				text-decoration: none; } } }

	&__phones {
		display: flex;
		flex-direction: column; } }




.header__logo {
	margin-top: 6px; }




