.quantity {
	display: flex;

	&__minus {
		display: flex;
		justify-content: center;
		align-items: center;
		padding-left: 6px;
		padding-right: 6px;
		background-color: transparent;
		border: 1px solid $border;
		border-top-left-radius: 4px;
		border-bottom-left-radius: 4px;
		width: 31px;
		height: 31px;
		color: $secondary-gray;
		font-size: 24px;
		box-sizing: border-box;
		cursor: pointer;

		&:focus {
			outline: none; } }

	&__plus {
		display: flex;
		justify-content: center;
		align-items: center;
		padding-left: 6px;
		padding-right: 6px;
		background-color: transparent;
		border: 1px solid $border;
		border-top-right-radius: 4px;
		border-bottom-right-radius: 4px;
		width: 31px;
		height: 31px;
		color: $secondary-gray;
		font-size: 24px;
		box-sizing: border-box;
		cursor: pointer;
		text-align: center;

		&:focus {
			outline: none; } }

	&__input {
		background-color: transparent;
		border: 1px solid $border;
		width: 43px;
		height: 31px;
		color: $primary-gray;
		font-size: 14px;
		border-radius: 0;
		box-shadow: none;
		box-sizing: border-box;
		text-align: center;
		-moz-appearance: textfield;


		&::-webkit-outer-spin-button {
			-webkit-appearance: none;
			margin: 0; }

		&::-webkit-inner-spin-button {
			-webkit-appearance: none;
			margin: 0; }


		&:focus {
			outline: none; } }

	&_sm {
		@media screen and (max-width:992px) {
			width: 33px; } } }

