.good {

	background-color: #fff;
	border: 1px solid $border;
	border-radius: 8px;
	overflow: hidden;
	padding: 5px 14px 14px 14px;
	background-color: #fff;

	display: flex;
	flex-direction: column;


	&-list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;

		&__text-wrapper {
			width: 100%;
			display: flex;
			margin-bottom: 5px; }

		&_text_center {
			margin-left: auto;
			margin-right: auto; } }

	&__header {
		display: flex;
		margin-bottom: 5px; }

	&__art {
		font-family: "RobotoRegular", sans-serif;
		color: $secondary-gray;
		font-size: 12px;
		line-height: 1rem;
		letter-spacing: 0.02em;
		margin-right: auto;

		&-num {
			color: $primary-color; } }

	&__tab {
		cursor: pointer;
		width: 22px;
		height: 20px;

		svg {
			width: 22px;
			height: 20px;
			fill:#d9d9d9 {} }

		&_active {

			svg {
				fill: $primary-color; } }


		&:hover {
			opacity: 0.7; } }


	&__compare {
		cursor: pointer;
		width: 22px;
		height: 20px;
		margin-left: 5px;

		svg {
			width: 22px;
			height: 20px;
			fill: #d9d9d9; }

		&_active {
			svg {
				fill: $primary-color; } }

		&:hover {
			opacity: 0.7; } }

	&__review-link {
		color: $primary-color;
		text-decoration: underline;
		margin-left: 20px;
		position: relative;
		margin-bottom: 5px;

		&::before {
			position: absolute;
			content: '';
			width: 13px;
			height: 11px;
			background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2213%22%20height%3D%2211%22%20viewBox%3D%220%200%2013%2011%22%20fill%3D%22none%22%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%3E%0A%3Cpath%20d%3D%22M12.4857%207.50428C12.8143%207.205%2012.9992%206.79929%2013%206.37605V1.59664C12.9992%201.17385%2012.8147%200.76854%2012.4868%200.469337C12.1588%200.170135%2011.7141%200.0013879%2011.2499%200H1.75294C1.28826%200.000694409%200.84284%200.169135%200.514266%200.468412C0.185691%200.76769%200.000762387%201.1734%200%201.59664V6.37868C0.000762387%206.80192%200.185691%207.20762%200.514266%207.5069C0.84284%207.80618%201.28826%207.97462%201.75294%207.97532H2.06143V11L5%207.97269H11.2471C11.7117%207.97199%2012.1572%207.80355%2012.4857%207.50428Z%22%20fill%3D%22%23EC6D26%22/%3E%0A%3C/svg%3E%0A');
			background-repeat: no-repeat;
			background-position: center;
			left: -20px;
			top: 4px; }

		&:hover {
			text-decoration: none; } }

	&__delivery-link {
		color: $primary-blue;
		text-decoration: underline;
		margin-left: 20px;
		position: relative;
		margin-bottom: 9px;

		&::before {
			position: absolute;
			content: '';
			width: 14px;
			height: 14px;
			background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2214%22%20height%3D%2214%22%20viewBox%3D%220%200%2014%2014%22%20fill%3D%22none%22%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%3E%0A%3Cpath%20d%3D%22M7%200C5.61553%200%204.26215%200.410543%203.11101%201.17971C1.95987%201.94888%201.06266%203.04213%200.532846%204.32122C0.00303297%205.6003%20-0.13559%207.00776%200.134506%208.36563C0.404603%209.7235%201.07129%2010.9708%202.05026%2011.9497C3.02922%2012.9287%204.2765%2013.5954%205.63437%2013.8655C6.99224%2014.1356%208.3997%2013.997%209.67878%2013.4672C10.9579%2012.9373%2012.0511%2012.0401%2012.8203%2010.889C13.5895%209.73784%2014%208.38447%2014%207C14%205.14348%2013.2625%203.36301%2011.9497%202.05025C10.637%200.737498%208.85652%200%207%200ZM7.875%2010.7187H6.125V6.34375H7.875V10.7187ZM7.875%205.03125H6.125V3.28125H7.875V5.03125Z%22%20fill%3D%22%233A6597%22/%3E%0A%3C/svg%3E%0A');
			background-repeat: no-repeat;
			background-position: center;
			left: -20px;
			top: 3px; }

		&:hover {
			text-decoration: none; } }



	&__link {
		text-align: center; }


	&__img {
		width: 100%;
		max-width: 160px;

		height: auto;
		max-height: 142px;
		align-self: center; }



	&__title {
		font-family: "RobotoRegular", Arial, sans-serif;
		font-size: 13px;
		line-height: 1em;
		letter-spacing: 0.02em;
		color: $primary-gray;
		margin-top: 3px;
		font-weight: normal;
		margin-bottom: 14px;
		max-width: 100%; }

	&__price {
		font-family: "RobotoMedium", Arial, sans-serif;
		font-size: 18px;
		line-height: 0.9em;
		color: $primary-gray;
		margin-top: 0;
		margin-bottom: 12px; }

	&__input {
		width: 40px;
		height: 24px;
		border: 1px solid $border;
		border-radius: 4px;
		color: $primary-gray;
		font-size: 14px;
		box-sizing: border-box;
		text-align: center;
		margin-left: 10px;
		-moz-appearance: textfield;


		&::-webkit-outer-spin-button {
			-webkit-appearance: none;
			margin: 0; }

		&::-webkit-inner-spin-button {
			-webkit-appearance: none;
			margin: 0; }


		&:focus {
			outline: none; } }

	&__button {
		width: 100%;
		line-height: 36px; }

	&__wrap {

		&-img {
			display: flex;
			align-items: center; }

		&-info {
			display: flex;
			flex-direction: column;
			margin-left: 30px;

			.text-light {
				margin-bottom: 5px; } } }

	&__banner {
		max-width: 100%; }


	&-wrap {
		border-bottom: 1px solid $border;
		padding-bottom: 35px; }







	&_col_4 {
		width: 24%;
		box-sizing: border-box;
		margin-bottom: 1.33%;

		&:not(:nth-child(4n+4)) {
			margin-right: 1.33%; } }


	&_col_2_sm {



		@media screen and (max-width:992px) {
			width: 46%;
			box-sizing: border-box;
			margin-bottom: 1.33%; }

		&:not(:nth-child(4n+4)) {
			@media screen and (max-width:992px) {
				margin-right: 0; } }

		&:not(:nth-child(2n+2)) {
			@media screen and (max-width:992px) {
				margin-right: 1.33%; } } }

	&_col_1_sm {
		@media screen and (max-width:580px) {
			width: 100%;
			box-sizing: border-box;
			margin-bottom: 1.33%;
			margin-right: 0; } }


	&_no-border {
		border: none;
		padding-left: 0;
		padding-right: 0; }

	&__list {
		display: flex; }


	&-info {
		&_margin-right_10 {
			margin-right: 10px; }

		&__row {
			display: flex;

			&_margin-bottom_10 {
				margin-bottom: 10px; }

			&_margin-bottom_18 {
				margin-bottom: 18px; } }

		&__price {
			font-family: "RobotoBold", sans-serif;
			color: $primary-gray;
			line-height: 0.9;
			font-size: 32px;
			margin: 0;
			margin-right: 12px; }

		&__nds {
			margin-bottom: 15px;
			margin-top: 3px; }

		&_col_3 {
			width: 30%;

			@media screen and (max-width:992px) {
				width: 40%; }

			@media screen and (max-width:767px) {
				width: 100%;
				margin-top: 20px; } }

		&__button {
			line-height: 54px;
			width: 100%;
			margin-bottom: 13px;

			@media screen and (max-width:992px) {
				line-height: 40px; } }

		&__click {
			line-height: 54px;
			width: 100%;
			border: 1px solid $primary-color;
			background-color: transparent;
			color: $primary-color;
			margin-bottom: 13px;

			@media screen and (max-width:992px) {
				line-height: 40px; }

			&:hover {
				background-color: $primary-color;
				color: #fff; } }

		&__inner {
			display: flex;
			margin-top: 10px;


			&-col {
				display: flex;
				flex-direction: column;
				align-items: flex-start;
				width: 50%;

				&_margin-bottom_10 > * {
					margin-bottom: 10px !important; }

				&_no-margin-top > * {
					margin-top: 0; }

				&_size_l {
					width: 60%; } } }

		&__column {
			display: flex;
			flex-direction: column; }

		&_align-center {
			align-items: center; }

		&_space-between {
			justify-content: space-between; }


		&_space-between {
			justify-content: space-between; } }

	&-present {
		width: 100%;
		display: flex;
		justify-content: space-between;
		margin-bottom: 21px;

		@media screen and (max-width:767px) {
			flex-direction: column; } }

	&_m {
		max-width: 213px;
		width: 100%;
		box-sizing: border-box;
		display: flex;
		flex-shrink: 0;
		margin-right: 15px; } }




.good__wrap-img .good__img {
	max-width: 74px;
	max-height: 67px; }


