.certificates {

	width: 100%;
	margin-bottom: 36px;


	&__list {
		display: flex;
		flex-wrap: wrap; }


	&__item {

		border-radius: 3px;
		background-color: #fff;
		position: relative;
		overflow: hidden;
		cursor: pointer;
		box-sizing: border-box;


		&::before {
			position: absolute;
			content: '';
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			background-color: transparent;
			transition: .4s ease-out; }

		&::after {
			transition: .4s ease-out; }

		&:hover {
			&::before {
				position: absolute;
				content: '';
				width: 100%;
				height: 100%;
				z-index: 1;
				background-color: rgba(0,0,0,.2); }

			&::after {
				width: 25px;
				height: 25px;
				position: absolute;
				content: '';
				z-index: 2;
				background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2225%22%20height%3D%2226%22%20viewBox%3D%220%200%2025%2026%22%20fill%3D%22none%22%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%3E%0A%3Cpath%20fill-rule%3D%22evenodd%22%20clip-rule%3D%22evenodd%22%20d%3D%22M24.55%2022.1L19.55%2017.1C22.65%2012.9%2022.25%207%2018.45%203.2C16.25%201.1%2013.55%200%2010.75%200C8.05%200%205.25%201.1%203.15%203.2C-1.05%207.4%20-1.05%2014.2%203.15%2018.4C5.25%2020.5%207.95%2021.6%2010.75%2021.6C12.95%2021.6%2015.15%2020.9%2017.05%2019.6L22.05%2024.6C22.35%2024.9%2022.85%2025.1%2023.25%2025.1C23.65%2025.1%2024.15%2024.9%2024.45%2024.6C25.15%2023.8%2025.15%2022.8%2024.55%2022.1ZM4.85%2016.7C1.55%2013.4%201.55%208.1%204.85%204.8C6.45%203.2%208.55%202.3%2010.75%202.3C12.95%202.3%2015.05%203.2%2016.65%204.8C19.95%208.1%2019.95%2013.4%2016.65%2016.7C15.05%2018.3%2012.95%2019.2%2010.75%2019.2C8.55%2019.2%206.45%2018.3%204.85%2016.7Z%22%20fill%3D%22white%22/%3E%0A%3C/svg%3E%0A');
				background-repeat: no-repeat;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%); } }






		&_col_6 {
			width: 15.3%;

			&:not(:nth-child(6n+6)) {
				margin-right: 1.6%; } }


		&_col_5 {
			width: 18%;
			margin-right: 1.5%;
			box-sizing: border-box;
			padding: 8px 10px;
			border-radius: 4px;
			margin-bottom: 12px; }

		&_col_3 {
			@media screen and (max-width:580px) {
				width: 48%; } } }

	&__img {
		max-width: 100%;
		height: 100%;
		overflow: hidden; } }

