.search {
	width: 100%;
	display: flex;

	&__input {
		width: 100%;
		height: 40px;
		padding-top: 11px;
		padding-bottom: 10px;
		padding-left: 20px;
		box-sizing: border-box;
		border: 1px solid $primary-color;
		border-right: none;
		border-bottom-left-radius: 4px;
		border-top-left-radius: 4px;
		box-sizing: border-box;

		font-family: "RobotoMedium", Arial, sans-serif;
		font-size: 14px;
		line-height: 1;
		letter-spacing: 0.02em;

		&::placeholder {
			color: $page-text;
			font-family: "RobotoRegular", Arial, sans-serif;
			font-size: 14px;
			line-height: 1;
			letter-spacing: 0.02em; }



		&:focus,:active {
			outline: none; } }


