.breadcrumbs {
	padding: 0;
	display: flex;
	margin-top: 15px;
	flex-wrap: wrap;

	&__item {
		list-style: none;
		position: relative;
		display: inline-block;
		margin-bottom: 4px;
		white-space: normal;



		&:not(:last-child) {
			margin-right: 18px;

			&::after {
				position: absolute;
				content: '';
				width: 8px;
				height: 14px;
				top: 3px;
				right: -13px;
				background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%228%22%20height%3D%2214%22%20viewBox%3D%220%200%208%2014%22%20fill%3D%22none%22%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%3E%0A%3Cpath%20d%3D%22M1.33333%20-5.72205e-05L7.99999%207L6.66666%208.40002L0%201.39995L1.33333%20-5.72205e-05Z%22%20fill%3D%22%23C4C4C4%22/%3E%0A%3Cpath%20d%3D%22M7.99999%207L1.33366%2014L0.000331107%2012.6L6.66699%205.59993L7.99999%207Z%22%20fill%3D%22%23C4C4C4%22/%3E%0A%3C/svg%3E%0A');
				background-repeat: no-repeat;
				background-position: center; } } }

	&__link {
		color: $primary-blue;
		font-family: "RobotoRegular", sans-serif;
		font-size: 14px;
		line-height: 1.2;
		letter-spacing: 0.02em;

		&:hover {
			text-decoration: none; } }

	&_margin-bottom_25 {
		margin-bottom: 25px; } }
