.tab {



	&__list {
		position: relative;
		z-index: 1;
		display: flex;
		justify-content: flex-start;
		padding: 0;
		margin-top: 0;

		&::after {
			position: absolute;
			content: '';
			width: 100%;
			height: 1px;
			background-color: $border;
			bottom: 0;
			left: 0;
			z-index: 2; } }



	&__item {
		list-style: none;
		background-color: #fff;
		border-top-left-radius: 8px;
		border-top-right-radius: 8px;
		border: 1px solid $border;
		display: flex;
		height: 44px;
		box-sizing: border-box;

		&:not(:last-child) {
			margin-right: 8px; }

		&_active {
			position: relative;
			z-index: 3;
			border: 1px solid $primary-color;


			.tab__link {
				color: $primary-color;
				font-family: "RobotoBold", Arial, sans-serif; } } }


	&__link {
		text-decoration: none;
		font-family: "RobotoRegular", Arial, sans-serif;
		font-size: 14px;
		line-height: 1em;
		letter-spacing: 0.02em;
		color: $primary-gray;
		height: 44px;
		box-sizing: border-box;
		padding: 13px 15px;

		@media screen and (max-width:777px) {
			font-size: 13px; } }

	&__content {
		padding: 0;
		display: flex;
		justify-content: space-between;
		margin-bottom: 8px;
		margin-top: -1px !important;

		&-item {
			list-style: none;
			display: flex;
			justify-content: space-between;
			width: 100%;
			display: none;

			&_active {
				display: flex; } } }


	&__title {
		margin-right: 30px; }

