.overlay {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
	background-color: rgba(0,0,0,.45);
	z-index: 1000;

	&__inner {
		position: relative;
		overflow-x: hidden;
		overflow-y: scroll;
		width: 100%;
		height: 100%;
		z-index: 2; } }


.popup {
	width: 96%;
	max-width: 970px;
	border-radius: 8px;
	margin: 1.5em auto;
	position: relative;
	background-color: #fff;
	padding: 26px 40px;
	box-sizing: border-box;

	@media screen and (max-width:767px) {
		padding: 14px 15px 35px; }

	@media screen and (max-width:460px) {
		padding-top: 30px; }

	&__close {
		position: absolute;
		content: '';
		width: 24px;
		height: 24px;
		background: url('../img/cross.svg') no-repeat center;
		top: 28px;
		right: 40px;
		transition: opacity .4s ease-out;

		@media screen and (max-width:460px) {
			top: 15px;
			right: 15px;
			width: 12px;
			height: 12px; }

		&:hover {
			cursor: pointer;
			opacity: 0.6; } }

	&_space-between {
		justify-content: space-between; }

	&__text {
		font-family: "RobotoMedium", sans-serif;
		font-size: 20px;
		color: $primary-gray;
		line-height: 1.2em;

		@media screen and (max-width:767px) {
			font-size: 16px; }

		&:first-child {
			margin-bottom: 10px; } }

	&__title {
		@media screen and (max-width:767px) {
			font-size: 16px; }

		&_m {
			margin-bottom: 35px;
			@media screen and (max-width:460px) {
				margin-top: 0;
				margin-bottom: 20px; } } }



	&__subtitle {
		margin-bottom: 16px; }

	&__button {
		line-height: 54px;
		width: 100%;
		margin-top: 30px;
		margin-bottom: 22px; }

	&__back {
		align-self: center;
		margin-bottom: 0; }

	&__row {
		display: flex;

		&_m {
			@media screen and (max-width:767px) {
				flex-direction: column;
				margin-top: 20px; } } }



	&__column {
		display: flex;
		flex-direction: column;

		&_margin-right_40 {
			margin-right: 40px; } }

	&__product {
		display: flex;
		max-width: 445px;
		width: 100%;
		margin-bottom: 30px;

		@media screen and (max-width:460px) {
			margin-bottom: 20px; } }

	&__cart-price {
		font-family: "RobotoMedium", sans-serif;
		font-size: 20px;
		color: #333; }

	&__cart-art {
		@media screen and (max-width:460px) {
			display: flex;
			flex-direction: column; } }

	&__cart-quantity {
		@media screen and (max-width:460px) {
			align-self: flex-end;
			margin-top: -34px; } }








	&__buy {
		max-width: 185px;
		width: 100%;
		line-height: 36px;
		align-self: flex-start;
		margin-left: auto;
		margin-right: 25px;

		.button__icon {
			@media screen and (max-width:922px) {
				margin-right: 0; } }

		@media screen and (max-width:992px) {
			margin-right: 0; }

		@media screen and (max-width:922px) {
			padding: 8px 22px;
			width: 66px; } }

	&__buy-btn {

		@media screen and (max-width:922px) {
			display: none; } }

	&__product-img {
		margin-right: 36px;
		width: 145px;
		height: 113px;

		@media screen and (max-width:850px) {
			width: 120px;
			height: 94px;
			margin-right: 17px; }

		@media screen and (max-width:460px) {
			width: 69px;
			height: 54px; } }

	&__product-descr {
		margin-top: 9px;
		line-height: 1.2rem;

		@media screen and (max-width:850px) {
			max-width: 200px; }

		&_m {
			margin-bottom: 35px;
			@media screen and (max-width:767px) {
				font-size: 16px; }
			@media screen and (max-width:460px) {
				margin-top: 0;
				margin-bottom: 0; } } }



	&__title_m {
		@media screen and (max-width:767px) {
			font-size: 16px;
			margin-bottom: 5px; } }


	&__product-info {
		max-width: 390px;
		width: 100%;
		background-color: #F9F9F9;
		border-radius: 8px;
		padding: 35px 50px;
		box-sizing: border-box;
		display: flex;
		flex-direction: column;

		@media screen and (max-width:950px) {
			padding: 23px 20px;
			max-width: 284px; }

		@media screen and (max-width:767px) {
			max-width: 100%;
			margin-top: 15px;
			margin-bottom: 20px; } }

	&__item-img {
		@media screen and (max-width:460px) {
			display: none; } } }




.popup__add .cart__item:last-child {
	border-bottom: 1px solid transparent !important; }

.unscrollable {
	overflow: hidden; }

