.link {
	color: $primary-blue;
	font-family: "RobotoRegular", sans-serif;
	font-size: 14px;
	line-height: 1.2;
	letter-spacing: 0.02em;
	margin-bottom: 15px;

	&:hover {
		text-decoration: none; }

	&-tab {
		width: 22px;
		height: 18px;
		margin-right: 8px;

		svg {
			width: 22px;
			height: 18px;
			fill: #d9d9d9; }

		&_active {

			svg {
				fill: $primary-color; } } }

	&_orange {
		display: inline-block;
		border-bottom: 1px dashed $primary-color;
		color: $primary-color;
		text-decoration: none;
		margin-bottom: 0;

		&:hover {
			border-bottom: 1px dashed transparent; } }

