	&_margin-right_40 {
		margin-right: 40px; }

	&_margin-bottom_30 {
		margin-right: 30px;
		display: inline-block; }

	&_no-margin-bottom {
		margin-bottom: 0; }

	&_margin-top_18 {
		margin-top: 18px; } }


.link-favorites {
	position: relative;
	color: $primary-gray;
	text-decoration: none;
	display: flex;
	align-items: center;


	&_margin-right_20 {
		margin-right: 20px; } }

.link-compare {
	margin-left: 30px;
	position: relative;
	color: $primary-gray;
	text-decoration: none;


	&::before {
		position: absolute;
		content: '';
		left: -30px;
		border: 1px solid $border;
		top: 0;
		width: 17px;
		height: 17px;
		border-radius: 2px;
		background-color: transparent; }

	&_active::after {
		position: absolute;
		content: '';
		width: 15px;
		height: 15px;
		background-color: $primary-color;
		border-radius: 2px;
		left: -28px;
		top: 2px; } }




