.m-good-tab {
	margin: 0;
	padding: 0;
	width: 100%;
	position: relative;
	margin-bottom: 25px;
	display: none;



	&_margin-top_20 {
		margin-top: 20px; }

	@media screen and (max-width:767px) {
		display: block; }

	&::before {
		position: absolute;
		content: '';
		width: 100%;
		height: 1px;
		top: 0;
		background-color: $border; }

	&__item {
		position: relative;
		padding: 0 15px;





		&::after {
			position: absolute;
			content: '';
			width: 100%;
			left: 0;
			height: 1px;
			background-color: $border; } }


