	&__content {
		line-height: 1.25rem;
		display: none;
		padding-bottom: 15px;

		&_active {
			display: block; } }

	&_sidebar {
		max-width: 250px;
		width: 100%;
		display: none;

		@media screen and (max-width:767px) {
			display: block; }

		&::before {
			display: none; } }

	&_card {
		background-color: transparent; }

	&_recomended {
		width: 100%;
		overflow-x: hidden;
		display: flex;
		white-space: nowrap; } }

.m-recomended {
	overflow-x: hidden;
	padding-left: 15px;
	margin-bottom: 25px;
	margin-top: 12px;
	display: none;

	@media screen and (max-width:767px) {
		display: block; }


	&__inner {
		overflow-x: scroll;
		display: flex; } }

.m-good-popular {
	padding-left: 15px;
	overflow-x: hidden;
	box-sizing: border-box;
	display: none;

	@media screen and (max-width:767px) {
		display: block; }

	&__list {
		flex-wrap: nowrap;
		overflow-x: scroll; }

	&__item {
		width: 102px;
		flex-shrink: 0; } }



