.collective {
	width: 100%;
	margin-bottom: 34px;

	&__inner {
		display: flex;
		justify-content: space-between; }


	&__video {
		width: 44.5%;

		@media screen and (max-width:460px) {
			width: 100%; }

		&-descr {
			font-family: "RobotoRegular", Arial, sans-serif;
			font-size: 14px;
			line-height: 1em;
			letter-spacing: 0.02em;
			color: $page-text;
			display: inline-block;
			margin-top: 16px;
			max-width: 291px; } }

	&__person {
		display: flex;
		flex-direction: column;
		width: 52.5%;

		&_company-page {
			margin-bottom: 25px; }

		&-data {
			display: flex; }

		&-img {
			border-radius: 4px;
			margin-right: 22px; }

		&-name {
			font-family: "RobotoMedium", Arial, sans-serif;
			font-size: 18px;
			line-height: 0.9em;
			color: $primary-gray;
			margin-bottom: 8px;
			margin-top: 0; }

		&-descr {
			font-family: "RobotoRegular", Arial, sans-serif;
			font-size: 14px;
			line-height: 1em;
			letter-spacing: 0.02em;
			color: $page-text; }

		&-inner {
			display: flex;
			flex-direction: column;
			justify-content: center;
			max-width: 250px; }

