
#slider-ui, #slider-ui-m {
	width: 100%;
	margin-bottom: 12px; }




.ui-slider {
	position: relative; }


.ui-slider .ui-slider-handle {
	position: absolute;
	z-index: 2;
	width: 6px;
	height: 15px;
	background: url('../img/toggle.svg') no-repeat;
	cursor: pointer; }

.ui-slider .ui-slider-range {
	position: absolute;
	z-index: 1;
	font-size: .7em;
	display: block;
	border: 0;
	overflow: hidden; }


.ui-slider-horizontal {
	height: 4px; }


.ui-slider-horizontal .ui-slider-handle {
	top: 4px;
	margin-left: -3px; }

.ui-slider-horizontal .ui-slider-range {
	top: 0;
	height: 100%; }

.ui-slider-horizontal .ui-slider-range-min {
	left: 0; }

.ui-slider-horizontal .ui-slider-range-max {
	right: 0; }


.ui-widget-content {
	background: $border; }


.ui-widget-header {
	background: $primary-color; }



