.m-footer {
	background-color: #282828;
	color: $border;
	text-align: center;
	font-family: "RobotoRegular", sans-serif;
	font-size: 12px;
	padding-top: 17px;
	padding-bottom: 17px;
	margin-top: 38px;
	display: none;

	@media screen and (max-width:992px) {
		display: block; } }
