.top-menu {
	background-color: $top-menu_background;

	&-container {
		max-width: 1200px;
		padding: 0 15px;
		width: 100%;
		box-sizing: border-box;
		margin-right: auto;
		margin-left: auto;
		display: flex;
		position: relative; }


	&__nav {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		min-height: 48px; }

	&__list {
		margin: 0;
		padding: 0;
		display: flex; }

	&__item {
		list-style: none;
		min-width: 122px;
		height: 100%;
		max-width: 132px; }

	&__sub {

		&-catalog {
			position: absolute;
			content: '';
			background-color: #fff;
			width: 245px;
			top: 100%;
			padding: 0;
			border-bottom-left-radius: 4px;
			border-bottom-right-radius: 4px;
			border: 1px solid #eeeeee;
			box-shadow: 0 2px 10px rgba(0,0,0,.1);
			display: none;
			z-index: 100; }

		&-item {
			display: flex;
			align-items: center;
			list-style: none;
			height: 68px;
			border-bottom: 1px solid #EEEEEE;
			box-sizing: border-box;
			border-left: 2px solid transparent;

			&:last-child {
				border-bottom: none; }

			&:hover {
				background-color: #f9f9f9;
				border-left: 2px solid $primary-color; }


			&:hover .top-menu__sub-inner {
				display: block; } }


		&-icon {
			height: 37px;
			width: 37px;
			padding-left: 67px;
			margin-right: 15px; }



		&-link {
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			border-left: 2px solid transparent;
			transition: all .4s ease-out;
			color: $primary-gray;
			text-decoration: none;
			padding-left: 67px;
			font-family: "RobotoRegular", sans-serif;
			font-size: 14px;
			letter-spacing: 0.02em;
			line-height: 1rem;

