		&-inner {
			width: 924px;
			box-sizing: border-box;
			background-color: #fff;
			position: absolute;
			content: '';
			left: 100%;
			top: 0;
			padding: 24px;
			padding-top: 5px;
			z-index: 1000;
			box-shadow: 0 2px 10px rgba(0,0,0,.1);
			display: none;

			@media screen and (max-width:1192px) {
				width: 824px; }

			@media screen and (max-width:1090px) {
				width: 724px; }

			.catalog {
				margin-top: 0; } } }






	&__catalog {
		margin: 0;
		padding: 0;
		display: flex;

		&-item {
			display: flex;
			height: 100%;
			position: relative;

			&_active {

				.top-menu__sub-catalog {
					display: block; } }


			&:hover .top-menu__sub-catalog {
				display: block; } } }

	&__link {
		position: relative;
		color: $top-menu_link;
		font-family: "RobotoBold",Arial, sans-serif;
		text-decoration: none;
		text-transform: uppercase;
		text-align: center;
		font-size: 12px;
		line-height: 1.2;
		height: 100%;
		width: 100%;
		box-sizing: border-box;
		display: flex;
		align-items: center;
		justify-content: center;
		letter-spacing: 0.14em;

		transition: background-color .4s ease-out;

		&:hover {
			background-color: $top-menu_link_hover; }

		&::after {
			position: absolute;
			content: '';
			height: 28px;
			width: 1px;
			background-color: #686868;
			right: 0; }


		&-catalog {
			position: relative;
			display: flex;
			justify-content: center;
			align-items: center;
			color: $top-menu_link;
			text-decoration: none;
			background-color: $primary-color;
			font-size: 20px;
			font-family: "RobotoMedium", Arial, sans-serif;
			text-transform: none;
			letter-spacing: normal;
			height: 100%;
			max-width: 245px;
			box-sizing: border-box;
			padding-left: 60px;
			padding-right: 110px;
			transition: background-color .4s ease-out;

			&:hover {
				background-color: $primary-color_hover; }

			&::before {
				position: absolute;
				content: '';
				width: 16px;
				height: 14px;
				background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2216%22%20height%3D%2214%22%20viewBox%3D%220%200%2016%2014%22%20fill%3D%22none%22%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%3E%0A%3Cpath%20d%3D%22M0%200H16V2H0V0Z%22%20fill%3D%22white%22/%3E%0A%3Cpath%20d%3D%22M0%206H16V8H0V6Z%22%20fill%3D%22white%22/%3E%0A%3Cpath%20d%3D%22M16%2012H0V14H16V12Z%22%20fill%3D%22white%22/%3E%0A%3C/svg%3E%0A');
				background-repeat: no-repeat;
				background-position: center;
				left: 28px;
				top: 18px; } } }
	&__cart {
		width: 100%;

		&-container {
			display: flex;
			flex-direction: column; }

		&-icon {
			margin-right: 9px;
			width: 26px;
			height: 31px;

			svg {
				width: 26px;
				height: 31px; } }


		&-link {
			position: relative;
			display: block;
			color: $top-menu_link;
			text-decoration: none;
			font-size: 12px;
			line-height: 1;
			height: 100%;


			display: flex;
			align-items: center;
			padding-left: 10px;

			box-sizing: border-box;
			transition: background-color .4s ease-out;

			&:hover {
				background-color: $top-menu_link_hover; }

			&::after {
				position: absolute;
				content: '';
				height: 100%;
				width: 1px;
				background-color: #686868;
				left: -1px; } }

		&-text {
			color: $primary-color;
			text-transform: uppercase;
			font-family: "RobotoBold", Arial, sans-serif;
			font-size: 12px;
			letter-spacing: 0.14em;
			line-height: 0.9;
			margin-bottom: 3px; }

		&-value {
			font-size: 12px;
			color: $cart-value; } } }

