.button {
  white-space: nowrap;
  text-decoration: none;
  cursor: pointer;
  border-radius: 4px;
  border: none;
  box-sizing: border-box;
  box-shadow: none;
  text-align: center;
  background-color: #EC6D26;
  color: #fff;
  font-family: "RobotoMedium", Arial, sans-serif;
  font-size: 16px;
  letter-spacing: 0.01em;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color .4s ease-out;
}

.button:hover {
  background-color: #f08d55;
  transition: background-color .4s ease-out;
}

.button__icon {
  width: 21px;
  height: 18px;
  margin-right: 10px;
}

.button__icon-feedback {
  width: 21px;
  height: 18px;
  margin-left: 10px;
}

.button_feedback {
  height: 54px;
  max-width: 270px;
}

.search {
  width: 100%;
  display: flex;
}

.search__input {
  width: 100%;
  height: 40px;
  padding-top: 11px;
  padding-bottom: 10px;
  padding-left: 20px;
  box-sizing: border-box;
  border: 1px solid #EC6D26;
  border-right: none;
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
  box-sizing: border-box;
  font-family: "RobotoMedium", Arial, sans-serif;
  font-size: 14px;
  line-height: 1;
  letter-spacing: 0.02em;
}

.search__input::placeholder {
  color: #818183;
  font-family: "RobotoRegular", Arial, sans-serif;
  font-size: 14px;
  line-height: 1;
  letter-spacing: 0.02em;
}

.search__input:focus,
.search__input :active {
  outline: none;
}

.search__button {
  height: 40px;
  width: 56px;
  background-color: #fff;
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
  border: 1px solid #EC6D26;
  border-left: none;
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2222%22%20height%3D%2222%22%20viewBox%3D%220%200%2022%2022%22%20fill%3D%22none%22%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%3E%0A%3Cpath%20d%3D%22M21.702%2020.0965L17.5962%2016.0333C20.7122%2012.1531%2020.4189%206.4426%2016.863%202.89185C14.9934%201.02496%2012.5373%200%209.89787%200C7.25844%200%204.76564%201.02496%202.89604%202.89185C1.02645%204.75874%200%207.21131%200%209.88353C0%2012.5557%201.02645%2015.0083%202.89604%2016.8752C4.76564%2018.7055%207.22178%2019.7671%209.89787%2019.7671C12.1341%2019.7671%2014.3336%2018.9983%2016.0565%2017.6073L20.1257%2021.6705C20.3456%2021.8902%2020.6022%2022%2020.8955%2022C21.1888%2022%2021.482%2021.8902%2021.6653%2021.6705C22.1052%2021.2313%2022.1052%2020.5358%2021.702%2020.0965ZM17.5962%209.88353C17.5962%2011.9334%2016.7897%2013.8735%2015.3234%2015.3012C13.857%2016.7288%2011.9508%2017.5707%209.89787%2017.5707C7.84498%2017.5707%205.90206%2016.7654%204.47237%2015.3012C3.04268%2013.8369%202.19953%2011.9334%202.19953%209.88353C2.19953%207.83361%203.00602%205.89351%204.47237%204.46589C5.93872%203.03827%207.80832%202.19634%209.89787%202.19634C11.9508%202.19634%2013.8937%203.00166%2015.3234%204.46589C16.7531%205.93012%2017.5962%207.79701%2017.5962%209.88353Z%22%20fill%3D%22%23818183%22/%3E%0A%3C/svg%3E%0A");
