.search__button:hover {
  cursor: pointer;
}

.search__button:focus {
  outline: none;
}

.search-section {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.filter {
  margin-left: 20px;
  margin-right: 13px;
}

.filter__button {
  width: 40px;
  height: 40px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.filter__button svg {
  fill: #818183;
  width: 22px;
  height: 22px;
}

.filter__button_not-active svg {
  fill: #EC6D26;
}

.filter__settings {
  position: absolute;
  height: 20px;
  background-color: red;
}

.filter-inner {
  width: 100%;
  display: flex;
  background-color: #fff;
  border: 1px solid #D9D9D9;
  border-radius: 8px;
  margin-top: 10px;
  padding: 10px 20px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  position: relative;
}

.filter-inner_not-active {
  display: none;
}

.filter-inner__item {
  margin: 0;
}

.filter__label {
  color: #333333;
  font-size: 16px;
  font-family: "RobotoBold", sans-serif;
  margin-right: 4px;
}

.filter__label_margin {
  margin-right: 23px;
}

.filter__select {
  appearance: none;
  border: none;
  width: 100%;
  height: 100%;
  font-size: 16px;
  cursor: pointer;
}

.filter__select:focus {
  outline: none;
}

.filter__select-container {
  height: 25px;
  width: 204px;
  margin-top: 3px;
  position: relative;
  margin-right: 60px;
}

.filter__select-container::before {
  position: absolute;
  content: '';
  width: 14px;
  height: 8px;
  background: url("../img/filter__arrow.svg") no-repeat center;
  top: 7px;
  right: 0;
  pointer-events: none;
}

.filter__brand {
  width: 72px;
}

.filter__range {
  margin-bottom: 0 !important;
}

.filter__price-label {
  margin-right: 42px;
}

.filter__price {
  color: #71717B;
}

.filter__price input[type=number] {
  text-align: center;
  padding: 0;
}

.arrow {
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  content: '';
  left: 30px;
  top: -15.5px;
  border: 8px solid transparent;
  border-bottom: 8px solid #fff;
}

.arrow::before {
  content: '';
  width: 13px;
  display: block;
  bottom: -4px;
  left: -2px;
  position: absolute;
  border-top: 1px solid #D9D9D9;
  transform: rotate(45deg);
}

.arrow::after {
  content: '';
  width: 13px;
  display: block;
  bottom: -4px;
  right: -2px;
  position: absolute;
  border-top: 1px solid #D9D9D9;
  transform: rotate(-45deg);
}

.slider {
  position: relative;
  margin-top: 15px;
  margin-bottom: 8px;
}

.slider__item {
  position: relative;
  height: 300px;
  background-color: #818183;
  border-radius: 4px;
  display: flex;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.slider__item svg {
  width: 36px;
  height: 8px;
}

.slider__item-link {
  position: absolute;
  max-width: 253px;
  text-decoration: none;
  left: 50px;
  bottom: 35px;
}

.slider__item-title {
  font-family: "RobotoMedium", sans-serif;
  font-size: 20px;
  line-height: 1;
  color: #fff;
  margin-bottom: 12px;
}

.slider__img {
  width: 100%;
  height: auto;
  max-width: 100%;
  min-height: 100%;
}

.slick-prev,
.slick-next {
  display: none !important;
}

.my-dots {
  position: absolute;
  bottom: 35px;
  right: 30px;
  display: flex;
  justify-content: flex-end;
  width: 100%;
  padding: 0;
  list-style: none;
  text-align: center;
}

.my-dots li {
  position: relative;
  display: inline-block;
  width: 7px;
  height: 7px;
  margin: 0 6px;
  padding: 0;
  cursor: pointer;
}

.my-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 7px;
  height: 7px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}

.my-dots li button:hover,
.my-dots li button:focus {
  outline: none;
}

.my-dots li button:hover:before,
.my-dots li button:focus:before {
  opacity: 1;
}

.my-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #c4c4c4;
  content: '';
}

.my-dots li.slick-active button:before {
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background-color: #c4c4c4;
  content: '';
  top: -2px;
}

.title {
  font-family: "RobotoMedium", Arial, sans-serif;
  font-size: 20px;
  line-height: 1;
  color: #333333;
  margin-bottom: 20px;
  font-weight: normal;
  margin-top: 18px;
}

.title__container {
  display: flex;
  align-items: center;
  margin-bottom: 4px;
}

.title_h1 {
  font-family: "RobotoBold", sans-serif;
  font-size: 28px;
  font-weight: normal;
  color: #343434;
  line-height: 1;
  margin-top: 14px;
}

.title__sum {
  font-size: 16px;
  margin-left: auto;
  font-weight: 300;
}

.title__description {
  font-family: "RobotoRegular", sans-serif;
  font-size: 14px;
  line-height: 1.2;
  letter-spacing: 0.02em;
  color: #333333;
  margin-bottom: 24px;
}

.title_popular {
  margin-right: 30px;
}

.title_margin-bottom_10 {
  margin-bottom: 10px;
}

.title_margin-bottom_25 {
  margin-bottom: 25px;
}

.title_margin-bottom_35 {
  margin-bottom: 35px;
}

.title_margin-bottom_40 {
  margin-bottom: 40px;
}

.title_no-margin-top {
  margin-top: 0;
}

.brand {
  width: 100%;
  margin-bottom: 20px;
}

.brand__list {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.brand__item {
  width: 18.5%;
  height: 76px;
  margin-bottom: 15px;
  padding: 6px;
  box-sizing: border-box;
  border: 1px solid #D9D9D9;
  border-radius: 8px;
  background-color: #fff;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.brand__img {
  max-width: 100%;
  max-height: 100%;
}

.news {
  width: 100%;
}

.news__list {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.news_border-bottom {
  position: relative;
  padding-bottom: 50px;
}

.news_border-bottom::after {
  position: absolute;
  content: '';
  width: 100%;
  height: 1px;
  bottom: 0;
  background-color: #D9D9D9;
}

.news-card {
  width: 24%;
  margin-bottom: 30px;
}

.news-card__title {
  font-family: "RobotoBold", Arial, sans-serif;
