.m-good-tab__content {
  line-height: 1.25rem;
  display: none;
  padding-bottom: 15px;
}

.m-good-tab__content_active {
  display: block;
}

.m-good-tab_sidebar {
  max-width: 250px;
  width: 100%;
  display: none;
}

.m-good-tab_sidebar::before {
  display: none;
}

.m-good-tab_card {
  background-color: transparent;
}

.m-good-tab_recomended {
  width: 100%;
  overflow-x: hidden;
  display: flex;
  white-space: nowrap;
}

.m-recomended {
  overflow-x: hidden;
  padding-left: 15px;
  margin-bottom: 25px;
  margin-top: 12px;
  display: none;
}

.m-recomended__inner {
  overflow-x: scroll;
  display: flex;
}

.m-good-popular {
  padding-left: 15px;
  overflow-x: hidden;
  box-sizing: border-box;
  display: none;
}

.m-good-popular__list {
  flex-wrap: nowrap;
  overflow-x: scroll;
}

.m-good-popular__item {
  width: 102px;
  flex-shrink: 0;
}

#slider-ui,
#slider-ui-m {
  width: 100%;
  margin-bottom: 12px;
}

.ui-slider {
  position: relative;
}

.ui-slider .ui-slider-handle {
  position: absolute;
  z-index: 2;
  width: 6px;
  height: 15px;
  background: url("../img/toggle.svg") no-repeat;
  cursor: pointer;
}

.ui-slider .ui-slider-range {
  position: absolute;
  z-index: 1;
  font-size: .7em;
  display: block;
  border: 0;
  overflow: hidden;
}

.ui-slider-horizontal {
  height: 4px;
}

.ui-slider-horizontal .ui-slider-handle {
  top: 4px;
  margin-left: -3px;
}

.ui-slider-horizontal .ui-slider-range {
  top: 0;
  height: 100%;
}

.ui-slider-horizontal .ui-slider-range-min {
  left: 0;
}

.ui-slider-horizontal .ui-slider-range-max {
  right: 0;
}

.ui-widget-content {
  background: #D9D9D9;
}

.ui-widget-header {
  background: #EC6D26;
}

.overlay-img {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.45);
  display: none;
  z-index: 1500;
}

.overlay-img__inner {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  max-width: 830px;
  width: 90%;
  height: auto;
}

.overlay-img__inner img {
  width: 100%;
  height: 100%;
}

.overlay-img__close {
  position: absolute;
  content: '';
  width: 24px;
  height: 24px;
  top: 0;
  right: -40px;
  background: url("../img/cross.svg") no-repeat center;
  cursor: pointer;
}

.overlay-img__close:hover {
  opacity: .7;
}

.overlay-img_active {
  display: block;
}

.page {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  max-width: 100%;
  font-family: "RobotoRegular", Arial, sans-serif;
  font-size: 14px;
  line-height: 1.2rem;
  color: #333333;
  background-color: #FBFBFB;
}

.main {
  margin-top: 15px;
  padding-bottom: 116px;
}

.main__container {
  max-width: 1200px;
  width: 100%;
  padding: 0 15px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
}

.content {
  display: flex;
  width: 100%;
  justify-content: space-between;
  box-sizing: border-box;
}

.content__container {
  max-width: 900px;
  width: 100%;
}

.content__container_no-sidebar {
  margin-left: auto;
}

.sidebar {
  width: 100%;
  max-width: 250px;
  margin-right: 20px;
}

.text-bold {
  font-family: "RobotoBold", Arial, sans-serif;
}

.padding-bottom_15 {
  padding-bottom: 15px;
}

.text-light {
  color: #818183;
}

.bold-text {
  font-family: "RobotoBold", sans-serif;
  color: #333333;
}

.text-uppercase {
  font-family: "RobotoMedium", sans-serif;
  line-height: 1.2rem;
  letter-spacing: 0.08rem;
  color: #333333;
  font-size: 14px;
  text-transform: uppercase;
}

.orange-text {
  color: #EC6D26;
}

.orange-text-medium {
  color: #EC6D26;
  font-family: "RobotoMedium", sans-serif;
}

